import React, { Component } from "react";
import Fade from "react-reveal";

class About extends Component {
  render() {
    if (!this.props.data) return null;

    // const name = this.props.data.name;
    const namemi = this.props.data.namemi;
    const profilepic = "images/" + this.props.data.image;
    const bio = this.props.data.bio;
    const lin=this.props.data.lin;
    // const street = this.props.data.address.street;
    const city = this.props.data.address.city;
    const state = this.props.data.address.state;
    // const zip = this.props.data.address.zip;
    // const phone = this.props.data.phone;
    const email = this.props.data.email;
    const resumeDownload = this.props.data.resumedownload;

    return (
      <section id="about">
        
        <Fade duration={1000}>
          <div className="row">
            <div className="three columns">
              <img
                className="profile-pic"
                src={profilepic}
                alt="Nordic Giant Profile Pic"
              />
            </div>
            <div className="nine columns main-col">
              <h2>About Me</h2>

              <p>{bio}</p>
              <div className="row" id="details">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span><i class="fa fa-user" aria-hidden="true"></i>{namemi}</span><br />

                    <span> <br /><i class="devicon-linkedin-plain colored"></i><a href="https://www.linkedin.com/in/javier-jaldo/" target="blank"> {lin}</a></span>
                    <br />
                    
          
                    <span>
                      {/* {street} */}
                      <br />
                      <i class="fa fa-map-marker" aria-hidden="true"></i> {city} {state}
                    </span>
                    <br />
                    {/* <span>{phone}</span> */}
                    <br />
                    <span> <i className="fa fa-envelope"></i>{email}</span>
                  </p>
                </div>
                <div className="columns download">
                  <p>
                    <a href={resumeDownload} className="button">
                      <i className="fa fa-download"></i>Download Resume
                    </a>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default About;
